.profile {
    width: 460px;
    margin: 0 auto;
    box-shadow: var(--box-shadow);
    font-weight: 700;
    background-color: var(--primary-background-color);
  
  }
  
  .profile:hover,
  .profile:focus {
    cursor: pointer;
    transition: color var(--animation-duration) var(--animation-timing-function);
    transform: scale(1.1);
  }
  
  .description {
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 30%;
    box-shadow: var(--box-shadow);
    margin-bottom: 30px;
    background-color: #fff6b2;
  }
  
  .name {
    font-weight: 700;
    font-size: 22px;
    margin-bottom: 10px;
  }
  
  .tag,
  .location {
    margin: 10px;
    color: var(--secondary-text-color); 
  }
  
  .statlist {
    display: flex;
    background:  var(--secondary-background-color);
    padding: 0;
    margin: 0;
  }
  
  .statItem {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    width: calc(100% / 3);
    border: 1px solid var(--border-color);
    padding: 20px 0;
  }
  
  .statItem:not(:last-child) {
    border-right: 2px solid var(--border-color);
  }
  
  
  .statlabel {
    margin-bottom: 4px;
    font-size: 18px;
    color: var(--secondary-text-color);
  
  }
  
  .quantity {
    font-size: 18px;
    color: var(--secondary-text-color);
  }
  